.homepage {
  display: flex;
  flex-direction: column;
  align-items: center;

  .directory-menu {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    .menu-item {
      width: 30%;
      height: 240px;
      flex: 1 1 auto;
      display: flex;
      align-items: center;
      justify-content: center;
      border: 1px solid black;
      margin: 7px;
      overflow: hidden;

      &:hover {
        cursor: pointer;

        & .background-image {
          transform: scale(1.1);
          transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
        }

        & .content {
          opacity: 0.9;
        }
      }

      &.large {
        height: 320px;
      }

      &:first-child {
        margin-right: 7.5px;
      }

      &.last-child {
        margin-left: 7.5px;
      }

      .background-image {
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: center;
      }

      & img {
        object-fit: cover;
        width: 100%;
        height: 240px;
      }

      .content {
        height: 90px;
        padding: 0 20px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border: 1px solid black;
        background-color: white;
        opacity: 0.5;
        position: absolute;

        .title {
          font-weight: bold;
          font-size: 20px;
          color: #4a4a4a;
        }

        .subtitle {
          font-weight: lighter;
          font-size: 14px;
        }
      }
    }
  }
}
